@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(home) {
  background-color: #1e1e1e;

  @include e(module-title) {
    margin-bottom: 35px;
    font-size: 24px;
    line-height: 24px;
    color: #fff;
    text-align: center;

    &::after,
    &::before {
      display: inline-block;
      width: 80px;
      height: 1px;
      vertical-align: middle;
      content: '\20';
      border-bottom: 1px solid #353535;
    }

    &::after {
      margin-left: 20px;
    }

    &::before {
      margin-right: 20px;
    }

    &.no-margin {
      margin-bottom: 0;
    }

    & + {
      @include e(module-subtitle) {
        margin-bottom: 35px;
      }
    }
  }

  @include e(module-subtitle) {
    margin-top: 12px;
    font-size: 13px;
    color: #999;
    text-align: center;
  }

  @include e(button) {
    position: relative;
    z-index: 1;
    width: 134px;
    height: 36px;
    font-size: 16px;
    line-height: 36px;
    color: $--color-primary;
    border: 1px solid $--color-primary;
    transition: color 0.6s cubic-bezier(0.8, 0, 0.2, 1);

    &::before {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      z-index: -1;
      content: '\20';
      background-color: #ba8d4d;
      opacity: 0.9;
      transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
      transform: scale(0, 1);
      transform-origin: right top;
    }

    &:hover {
      color: #fff;

      &::before {
        transform: scale(1, 1);
        transform-origin: left top;
      }
    }
  }

  @include e(banner) {
    height: 480px;

    :global(.swiper-pagination) {
      bottom: 80px;
    }
  }

  @include e(games) {
    position: relative;
    z-index: 2;
    width: $--content-width;
    margin: -60px auto 0;
  }

  @include e(dew) {
    width: $--content-width;
    margin: 65px auto 0;
  }

  @include e(baccarat) {
    width: $--content-width;
    margin: 65px auto 0;
  }

  @include e(promo) {
    padding: 50px 0 65px;
    margin-top: 65px;
    background-color: #282828;
  }

  @include e(vip) {
    width: $--content-width;
    margin: 65px auto 0;
  }

  @include e(group) {
    margin-top: 65px;
  }
}

%item {
  position: relative;
  width: 594px;
  height: 230px;
  overflow: hidden;
}

%item-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  visibility: hidden;
  background-color: rgba(36, 36, 36, 0.5);
  opacity: 0;
  transition: all 0.2s;
}

%item-img {
  width: auto;
  height: 100%;
  vertical-align: top;
  transition: transform 0.5s ease-in;
}

%item-info {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  height: 100%;
}

%item-button {
  position: absolute;
  bottom: 30px;
  visibility: hidden;
  opacity: 0;
  transition: visibility, opacity 0.2s, color 0.6s cubic-bezier(0.8, 0, 0.2, 1);
}

@include b(games) {
  @include e(item) {
    @extend %item;

    box-shadow: 2px 20px 20px #161616;

    &:not(.disable):hover {
      @include e(img) {
        transform: scale(1.05);
      }

      @include e(mask) {
        visibility: visible;
        opacity: 1;
      }

      @include e(button) {
        visibility: visible;
        opacity: 1;
      }

      @include e(online) {
        visibility: hidden;
        opacity: 0;
      }
    }
  }

  @include e(img) {
    @extend %item-img;
  }

  @include e(info) {
    @extend %item-info;

    padding: 26px 0 0 26px;
  }

  @include e(title) {
    margin-bottom: 20px;
    font-size: 24px;
    line-height: 1;
    color: #fff;
  }

  @include e(icon-exclusive) {
    position: relative;
    top: -2px;
    width: auto;
    height: 20px;
    margin-left: 6px;
    vertical-align: bottom;
  }

  @include e(subtitle) {
    font-size: 16px;
    line-height: 22px;
    color: #999;

    & + & {
      margin-top: 6px;
    }
  }

  @include e(mask) {
    @extend %item-mask;
  }

  @include e(button) {
    @extend %item-button;

    left: 26px;
  }

  @include e(online) {
    position: absolute;
    bottom: 39px;
    left: 25px;
    z-index: 1;
    font-size: 16px;
    line-height: 20px;
    color: $--color-primary;
    visibility: visible;
    opacity: 1;
    transition: visibility, opacity 0.2s;
  }

  @include e(icon-online) {
    display: inline-block;
    width: 29px;
    height: 20px;
    margin-right: 12px;
    vertical-align: top;
    background-position: -79px 0;

    @include background-image(url(~@/assets/img/common/common_icons.png));
  }

  @include e(disable) {
    @extend %item-mask;

    z-index: 3;
    font-size: 18px;
    color: #999;
    text-align: center;
    visibility: visible;
    opacity: 1;
  }

  @include e(icon-time) {
    display: inline-block;
    width: 35px;
    height: 40px;
    margin-top: (230px - 70) / 2;
    margin-bottom: 10px;
    vertical-align: top;
    background-position: -116px -247px;

    @include background-image(url(~@/assets/img/common/common_icons.png));
  }
}

@include b(vip) {
  @include e(item) {
    @extend %item;

    &:hover {
      @include e(img) {
        transform: scale(1.05);
      }

      @include e(mask) {
        visibility: visible;
        opacity: 1;
      }

      @include e(button) {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  @include e(img) {
    width: auto;
    height: 100%;
    vertical-align: top;
    transition: transform 0.5s ease-in;
  }

  @include e(info) {
    @extend %item-info;

    padding: 22px 0 0 28px;
  }

  @include e(title) {
    font-size: 24px;
    color: #fff;

    & + {
      @include e(subtitle) {
        margin-top: 10px;
      }
    }
  }

  @include e(subtitle) {
    font-size: 16px;
    color: #999;
  }

  @include e(mask) {
    @extend %item-mask;
  }

  @include e(button) {
    @extend %item-button;

    left: 28px;
  }
}

@include b(group) {
  background: no-repeat center;

  //@include background-image(url(~@/views/home/img/group_bg.jpg));
  @include e(inner) {
    width: $--content-width;
    height: 245px;
    padding-top: 65px;
    margin: 0 auto;
    overflow: hidden;
  }

  @include e(icons) {
    height: 48px;
    line-height: 48px;
  }

  @include e(icon) {
    display: inline-block;
    width: 200px;
    height: 42px;
    vertical-align: bottom;

    @include background-image(url(~@/assets/img/common/kf_logos.png));

    &.video-game {
      background-position: 0 0;
    }

    &.e-sports {
      background-position: 0 -229px;
    }

    &.chess {
      height: 48px;
      background-position: 0 -320px;
    }

    &.vina {
      background-position: 0 -182px;
    }

    &.media {
      height: 44px;
      background-position: 0 -138px;
    }

    &.fund {
      background-position: 0 -94px;
    }
  }
}
