@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(banner) {
  height: 100%;
  min-height: 300px;

  @include e(item) {
    overflow: hidden;
  }

  @include e(img) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }

  :global(.swiper-pagination-bullet) {
    width: 12px;
    height: 12px;
    background-color: transparent;
    border: 2px solid #666;
    opacity: 1;
  }

  :global(.swiper-pagination-bullet-active) {
    background-color: $--color-primary;
    border: 2px solid transparent;
  }
}
