@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(page) {
  height: 100%;

  @include e(content) {
    height: 100%;
    transition: transform 0.3s ease;

    &.open-side-bar {
      transform: translateX($--side-bar-width);
    }
  }

  &.padding-top-normal {
    padding-top: $--header-height+$--nav-bar-height;
  }

  &.padding-top-promo {
    padding-top: $--header-height;
  }
}
