@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(baccarat) {
  color: #fff;

  @include e(box) {
    position: relative;
    width: 1200px;
    height: 428px;
    padding: 20px;
    margin: auto;
    overflow: hidden;
    background: #80a9ff linear-gradient(to bottom right, #80a9ff, #4444c3) no-repeat;

    //@include background-image(url(~@/views/home/img/baccarat_bg.jpg));
  }

  @include e(triangle) {
    position: absolute;
    top: -45px;
    right: -45px;
    width: 90px;
    height: 90px;
    padding-bottom: 4px;
    text-align: center;
    background-color: #cca352;
    transition: background-color 0.3s ease-in-out;
    transform: rotate(45deg);

    &:hover {
      background-color: #bb8c4d;
    }
  }

  @include e(triangle-text) {
    position: absolute;
    bottom: 2px;
    left: 0;
    width: 100%;
    text-align: center;
  }

  @include e(withdraw-news) {
    margin-top: 25px;
    font-size: 16px;
    color: #b0b0b0;
    text-align: center;
  }

  @include e(withdraw-link) {
    color: $--color-primary;
    text-decoration: underline;
  }
}

%rank-icons {
  background: no-repeat;

  @include background-image(url(~@/views/home/img/baccarat_icons.png));
}

@include b(rank) {
  float: left;
  width: 394px;

  @at-root {
    %rank-cell {
      display: inline-block;
      vertical-align: top;
    }
  }

  @include e(item) {
    padding: 0 20px;
    font-size: 14px;
    text-align: center;

    @include m(title) {
      height: 38px;
      font-weight: 700;
      line-height: 38px;
      background-color: rgba(0, 0, 0, 0.4);
    }

    @include m(content) {
      position: relative;
      height: 35px;
      line-height: 35px;
      cursor: pointer;
      background-color: rgba(0, 0, 0, 0.1);
      transition: background-color 0.2s ease;

      &::before {
        position: absolute;
        top: 50%;
        right: (20px - 5) / 2;
        width: 5px;
        height: 9px;
        margin-top: -4px;
        content: '\20';
        background-position: 0 -30px;
        opacity: 0;
        transform: translateX(-10px);

        @extend %rank-icons;
      }

      &.active {
        font-weight: 700;
        color: #668cff;
        background: hsla(0, 0%, 100%, 0.8);

        &::before {
          opacity: 1;
          transition: all 0.3s ease-in-out;
          transform: translateX(0);
        }
      }

      &:hover:not(.active) {
        background-color: hsla(0, 0%, 100%, 0.1);
      }

      &:not(.active) + &:not(.active) {
        &::after {
          position: absolute;
          top: 0;
          right: 20px;
          left: 20px;
          height: 1px;
          content: '\20';
          border-top: 1px solid #9cb7ed;
        }
      }
    }
  }

  @include e(rank-column) {
    @extend %rank-cell;

    width: 20%;
  }

  @include e(username-column) {
    @extend %rank-cell;

    width: 24%;
  }

  @include e(profit-column) {
    @extend %rank-cell;

    width: 36%;
  }

  @include e(ratio-column) {
    @extend %rank-cell;

    width: 20%;
  }

  @include e(rank) {
    display: inline-block;
    width: 22px;
    margin-right: 10px;
    font-size: 14px;
    vertical-align: middle;

    &.rank-1 {
      @extend %rank-icons;

      height: 27px;
      background-position: 0 0;
    }

    &.rank-2 {
      @extend %rank-icons;

      height: 27px;
      background-position: -23px 0;
    }

    &.rank-3 {
      @extend %rank-icons;

      height: 27px;
      background-position: -46px 0;
    }
  }

  @include e(status) {
    display: inline-block;
    width: 12px;
    vertical-align: middle;

    &.up {
      height: 18px;
      background-position: -70px 0;

      @extend %rank-icons;
    }

    &.down {
      height: 18px;
      background-position: -83px -1px;

      @extend %rank-icons;
    }

    &.none {
      height: 2px;
      background-color: #4d659e;
      border-radius: 1px;
    }
  }
}

@include b(details) {
  padding: 0 30px;
  margin-left: 394px;

  @include e(title) {
    height: 40px;
    font-size: 14px;
    line-height: 40px;
    text-align: center;

    &::before,
    &::after {
      display: inline-block;
      width: 114px;
      height: 1px;
      vertical-align: middle;
      content: '\20';
      background: no-repeat left center;
    }

    &::before {
      margin-right: 10px;

      @include background-image(url(~@/views/home/img/line_left.png));
    }

    &::after {
      margin-left: 10px;

      @include background-image(url(~@/views/home/img/line_right.png));
    }

    @include m(second) {
      margin-top: 25px;
    }
  }

  @include e(profit) {
    position: relative;
    width: 548px;
    height: 84px;
    padding-left: 100px;
    margin: 17px auto 0;
    font-size: 44px;
    font-weight: 700;
    line-height: 82px;
    letter-spacing: 1px;
    background-color: rgba(0, 0, 0, 0.3);
    border: 1px solid #fff;
    border-radius: 42px;
  }

  @include e(rank) {
    position: absolute;
    top: -4px;
    left: -10px;
    display: block;
    width: 80px;
    height: 100px;
    background-position: 0 100px;

    @extend %rank-icons;

    &.rank-1 {
      background-position: 0 -69px;
    }

    &.rank-2 {
      background-position: -81px -69px;
    }

    &.rank-3 {
      background-position: -162px -69px;
    }

    &.rank-4 {
      background-position: -245px -69px;
    }

    &.rank-5 {
      background-position: -326px -69px;
    }

    &.rank-6 {
      background-position: 0 -169px;
    }

    &.rank-7 {
      background-position: -81px -169px;
    }

    &.rank-8 {
      background-position: -162px -169px;
    }

    &.rank-9 {
      background-position: -243px -169px;
    }

    &.rank-10 {
      background-position: -324px -169px;
    }
  }

  @include e(status) {
    display: inline-block;
    width: 26px;
    height: 32px;
    margin-top: -8px;
    margin-left: 10px;
    vertical-align: middle;
    background-position: 0 32px;

    @extend %rank-icons;

    &.up {
      background-position: -9px -30px;
    }

    &.down {
      background-position: -37px -31px;
    }

    &.none {
      width: 24px;
      height: 4px;
      background-position: -64px -31px;
    }
  }

  @include e(hot) {
    margin-top: 40px;
    font-size: 18px;
    line-height: 38px;
    text-align: center;
  }

  @include e(button) {
    margin-left: 35px;
  }

  @include e(tables) {
    font-size: 14px;
    text-align: center;
  }

  @include e(record) {
    height: 34px;
    line-height: 34px;
  }

  @include e(text-link) {
    font-weight: 700;
    text-decoration: underline;
  }

  @include e(result) {
    display: inline-block;
    min-width: 26px;
    height: 26px;
    padding: 0 5px;
    margin: 0 5px;
    font-size: 12px;
    font-weight: 700;
    line-height: 26px;
    text-align: center;
    vertical-align: middle;
    border-radius: 26px;

    &.banker {
      background-color: #d55263;
    }

    &.player {
      background-color: #6e8df7;
    }

    &.tie {
      background-color: #4ea950;
    }
  }

  @include e(amount) {
    font-weight: 700;
    color: $--color-primary;
  }
}
