@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(loading) {
  @include mask(999);

  @include e(logo) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 83px;
    height: 83px;
    margin: auto;
    background: no-repeat left center;
    animation: rotate 1s steps(12) infinite;

    @include background-image(url(~@/assets/img/components/loading/sprites.png));

    @keyframes rotate {
      0% {
        background-position: 0 0;
      }

      100% {
        background-position: -996px 0;
      }
    }
  }

  &.enter {
    opacity: 0;
  }

  &.enter-active {
    opacity: 1;
    transition: all 0.3s ease;
  }

  &.exit {
    opacity: 1;
    transition: all 0.3s ease;
  }

  &.exit-active {
    opacity: 0;
  }
}
