@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
$--input-width: 300px;
$--input-height: 40px;
$--code-button-width: 96px;
$--input-prefix-width: 42px;
$--input-suffix-width: 24px;
$--icon-url: url(~@/assets/img/common/common_icons_1.png);

@include b(login) {
  @include mask(19);

  @include e(box) {
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    width: 392px;
    margin: -180px auto 0;
    font-size: 14px;
    color: #999;
    background-color: #fff;
    box-shadow: -1px -1px 15px rgba(0, 0, 0, 0.7);
  }

  @include e(header) {
    position: relative;
    height: 64px;
    font-size: 16px;
    line-height: 63px;
    color: $--color-primary;
    text-align: center;
    border-bottom: 1px solid $--color-primary;

    @include background-image(url(~@/assets/img/components/login_dialog/header_bg.jpeg));
  }

  @include e(close) {
    position: absolute;
    top: 0;
    right: 0;
    width: 30px;
    height: 30px;
    font-size: 20px;
  }

  @include e(content) {
    width: $--input-width;
    margin: 32px auto;
  }

  @include e(error) {
    position: relative;
    min-height: 24px;
    padding: 3px 0 3px 16px + 12 * 2;
    font-size: 12px;
    line-height: 18px;
    color: #eb4b4b;
    background-color: #ffe5e6;
    border: 1px solid #eb4b4b;
    border-radius: 2px;
  }

  @include e(icon-error) {
    position: absolute;
    top: 50%;
    left: 12px;
    width: 16px;
    height: 16px;
    margin-top: -8px;
    background-position: -258px -45px;

    @include background-image($--icon-url);
  }

  @include e(input-wrapper) {
    position: relative;
    overflow: hidden;

    & + & {
      margin-top: 12px;
    }
  }

  @include e(input) {
    width: $--input-width;
    height: $--input-height;
    padding-right: $--input-suffix-width + 10;
    padding-left: $--input-prefix-width + 10;
    font-weight: 700;
    line-height: $--input-height;
    color: #000;
    border: 1px solid #b7b7b7;
    transition: border 1s ease;

    &:hover,
    &:focus {
      border: 1px solid #b79675;
    }

    &::placeholder {
      color: #b1b1b1;
    }

    @include m(code) {
      width: $--input-width - $--code-button-width -4;
    }
  }

  @include e(code) {
    float: right;
    width: $--code-button-width;
    height: $--input-height;
    cursor: pointer;
  }

  @include e(prefix) {
    position: absolute;
    top: 50%;
    z-index: 2;
    height: 18px;
    margin-top: -9px;

    @include background-image($--icon-url);

    @include m(user) {
      left: ($--input-prefix-width / 2) - (16 / 2);
      width: 16px;
      background-position: -276px -45px;
    }

    @include m(password) {
      left: ($--input-prefix-width / 2) - (14 / 2);
      width: 14px;
      background-position: -298px -95px;
    }

    @include m(verify) {
      left: ($--input-prefix-width / 2) - (14 / 2);
      width: 14px;
      background-position: -298px -76px;
    }

    @include m(line) {
      left: $--input-prefix-width;
      height: 20px;
      margin-top: -10px;
      border-right: 1px solid #e4e4e4;
    }
  }

  @include e(suffix) {
    position: absolute;
    top: 50%;
    z-index: 2;
    cursor: pointer;

    @include background-image($--icon-url);

    @include m(cancel) {
      right: 10px;
      width: 14px;
      height: 14px;
      margin-top: -7px;
      font-size: 14px;
      font-weight: 700;
      line-height: 11px;
      color: #fff;
      text-align: center;
      background: #bd9570 none;
      border-radius: 50%;
    }

    @include m(eye-close) {
      right: $--code-button-width + 10 + 4;
      width: 20px;
      height: 8px;
      margin-top: -4px;
      background-position: -180px -88px;
    }

    @include m(eye-open) {
      right: $--code-button-width + 10 + 4;
      width: 24px;
      height: 16px;
      margin-top: -8px;
      background-position: -232px -45px;
    }
  }

  @include e(forgot) {
    margin-top: 8px;
    font-size: 13px;
    line-height: 18px;
    text-align: right;
  }

  @include e(link) {
    text-decoration: underline;

    &:hover {
      color: $--color-primary;
    }
  }

  @include e(button) {
    display: block;
    margin: 22px 0 14px;

    @include button();
    @include button-solid();
  }

  @include e(registered) {
    text-align: center;
  }
}
