@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@import "variables";
@import "functions";
@import "mixin";

img[lazy=error],
img[lazy=loading],
img[lazy=_error],
img[lazy=_loading],
img.swiper-lazy-loading {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 26px;
  height: 26px;
  margin: auto;
  background-color: transparent;
  border: 2px solid #f5b607;
  border-bottom-color: transparent;
  border-radius: 50%;
  transform: none;
  animation: rotate 0.75s linear infinite;
}

#root {
  position: relative;
  min-width: $--content-width;
  height: 100%;
}

.fl {
  float: left;
}

.fr {
  float: right;
}

.clearfix {
  zoom: 1;

  &::after {
    display: block;
    height: 0;
    clear: both;
    content: "\20";
  }
}

.hide {
  display: none;
}

.default-bg {
  background: $--background-color-base no-repeat center;

  @include background-image(url(~@/assets/img/common/default-img.png));
}

.default-bg-small {
  background: $--background-color-base no-repeat center;

  @include background-image('~@/assets/img/common/default-img-small.png');
}

.c-primary {
  color: $--color-primary;
}

.c-gray {
  color: $--color-gray;
}

.monospaced-font {
  font-family: Helvetica Neue, Microsoft YaHei, sans-serif;
}

.maintain-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 5;
  background-color: rgba(0, 0, 0, 0.6);
  transform: translateZ(0);

  &__inner {
    position: relative;
    top: 50%;
    color: #999;
    text-align: center;
    transform: translateY(-50%);
  }

  &__icon {
    display: inline-block;
    width: rem(22);
    height: rem(26);
    vertical-align: top;
    background: url(~@/assets/img/common/icon_time.png) no-repeat center/100%;
  }

  &__text {
    margin-top: rem(4);
    font-size: rem(14);
  }
}

.fade-enter {
  opacity: 0;
}

.fade-exit,
.fade-enter-active {
  opacity: 1;
  transition: opacity 0.3s ease;
}

.fade-exit-active {
  opacity: 0;
}

.alert-enter {
  opacity: 0;
  transform: scale(0.9);
}

.alert-exit,
.alert-enter-active {
  opacity: 1;
  transition: all 300ms;
  transform: scale(1);
}

.alert-exit-active {
  opacity: 0;
  transform: scale(0.9);
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
