@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(dew) {
  @include e(item) {
    display: inline-block;
    width: 398px;
    margin: 0 auto;
    font-size: 16px;
    vertical-align: top;
    background-color: #141414;

    & + & {
      margin-left: ($--content-width - 398 * 3) / 2;
    }
  }

  @include e(header) {
    padding: 16px 20px 0;
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 20px;
    color: #fff;
  }

  @include e(timer) {
    float: right;
    color: #697081;
  }

  @include e(icon-time) {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin-right: 8px;
    vertical-align: top;
    background: no-repeat center /auto 100%;

    @include background-image(url(~@/assets/img/common/icon_time.png));
  }

  @include e(scoreboard) {
    width: 304px;
    padding: 8px;
    margin: 0 auto 18px;
    text-align: center;
    border: 1px solid #31353d;
    border-radius: 20px;
  }

  @include e(scoreboard-item) {
    box-sizing: border-box;
    float: left;
    width: 33.33%;
    height: 18px;
    font-weight: 700;
    line-height: 18px;

    & + & {
      border-left: 1px solid #31353d;
    }

    &:first-child {
      color: #e54560;
    }

    &:nth-child(2) {
      color: #24b253;
    }

    &:last-child {
      color: #668cff;
    }

    span:first-child {
      margin-right: 3px;
    }
  }

  @include e(canvas-wrapper) {
    position: relative;
    width: 385px;
    height: 145px;
    margin: 0 auto;
    text-align: center;
    background-color: #0e0e0e;
  }

  @include e(canvas) {
    width: 385px;
    height: 145px;
    vertical-align: top;
  }

  @include e(shuffle) {
    @extend %canvas-mask;

    background: #000 no-repeat center -18px;

    @include background-image(url(~@/views/home/img/shuffle.gif));
  }

  @include e(loading) {
    @extend %canvas-mask;

    background: #000 no-repeat center 45px;

    @include background-image(url(~@/views/home/img/loading.gif));
  }

  @include e(shuffle-text) {
    position: relative;
    top: 88px;
  }

  @include e(loading-text) {
    position: relative;
    top: 72px;
  }

  @include e(button-wrapper) {
    margin: 18px 0;
    text-align: center;
  }
}

%canvas-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 3;
  color: #938f8b;
  text-align: center;
}
