@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
$--z-index: 99;

@include b(side-bar) {
  user-select: none;

  @include e(inner) {
    position: fixed;
    top: 0;
    left: 0;
    z-index: $--z-index;
    width: $--side-bar-width;
    height: 100%;
    overflow: hidden;
    color: #999;
    background-color: #3a3a3a;
    transition: transform 0.3s ease;
    transform: translateX(-$--side-bar-width);
  }

  @include e(scroll) {
    width: $--side-bar-width + 20;
    height: 100%;
    overflow: auto;
  }

  @include e(content) {
    position: relative;
    width: $--side-bar-width;
    min-height: 100%;
  }

  @include e(mask) {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: $--z-index - 1;
    background-color: rgba(0, 0, 0, 0.7);
    opacity: 0;
    transition: opacity 0.3s ease;
  }

  &.open {
    visibility: visible;
    opacity: 1;

    @include e(inner) {
      transform: translateX(0);
    }

    @include e(mask) {
      opacity: 1;
    }
  }

  &.hide {
    position: relative;
    z-index: -1;
    visibility: hidden;
    opacity: 0;
  }
}

@include b(header) {
  height: 70px;
  padding: 0 18px 0 38px;
  font-size: 13px;
  line-height: 70px;
  color: #adadad;
  border-bottom: 1px solid #434343;

  @include e(link) {
    text-decoration: underline;
  }

  @include e(close) {
    float: right;
    height: 100%;
    transition: color 0.2s ease;

    &:hover {
      color: #969696;
    }
  }

  @include e(font-icon) {
    position: relative;
    top: -2px;
    margin-left: 6px;
    font-size: 32px;
    vertical-align: top;
  }
}

@include b(module) {
  padding: 22px 18px 22px 38px;
  border-bottom: 1px solid #434343;
  transition: background-color 0.2s ease;

  @include e(title) {
    font-size: 16px;
    line-height: 20px;
    color: #adadad;
  }

  @include e(title-link) {
    float: right;
    font-size: 12px;
    font-weight: 400;
    transition: color 0.2s ease;

    &:hover {
      color: #969696;
    }
  }

  @include e(icon-checked) {
    float: right;
    width: 32px;
    height: 32px;
    margin-top: 4px;
    zoom: 0.5;
    background: no-repeat -116px -207px;

    @include background-image(url(~@/assets/img/common/common_icons.png));
  }

  @include e(content) {
    margin-top: 8px;
    font-size: 12px;
    line-height: 20px;

    a {
      display: inline-block;
      margin-right: 8px;
      transition: color 0.2s ease;

      &:hover {
        color: $--color-primary;
      }
    }
  }

  &:hover {
    background-color: #2b2b2b;
  }
}

@include b(download) {
  padding: 22px 18px 22px 38px;

  @include e(title) {
    font-size: 16px;
    color: #999;
    text-align: center;
  }

  @include e(qr-wrapper) {
    position: relative;
    box-sizing: content-box;
    width: 124px;
    height: 124px;
    padding: 10px;
    margin: 10px auto;
    font-size: 0;
    background-color: #fff;
    border: 8px solid #747474;
  }

  @include e(logo) {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 28px;
    height: 28px;
    margin-top: -14px;
    margin-left: -14px;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.8);
  }
}

@include b(language) {
  position: absolute;
  top: 70px;
  bottom: 0;
  left: -$--side-bar-width;
  width: $--side-bar-width;
  background-color: #3a3a3a;
  transition: left 0.3s ease;

  &.open {
    left: 0;
  }
}
