@import "~@/assets/scss/production.scss";@import "~@/assets/scss/variables.scss";@import "~@/assets/scss/functions.scss";@import "~@/assets/scss/mixin.scss";
@include b(promo) {
  width: $--content-width;
  margin: 0 auto;
  color: #fff;

  @include e(title) {
    height: 40px;
    font-size: 24px;
    line-height: 40px;
    color: $--color-primary;
    text-align: center;
  }

  @include e(line) {
    box-sizing: content-box;
    width: 495px;
    padding-top: 3px;
    margin-top: (40px - 8) / 2;
    border-top: 1px solid #3e3e3e;

    &::after {
      display: block;
      width: 100%;
      height: 4px;
      content: '\20';
      background-color: #3e3e3e;
    }
  }

  @include e(amount) {
    margin-top: 20px;
    font-size: 82px;
    color: $--color-primary;
    text-align: center;
  }

  @include e(dot-line) {
    height: 10px;
    margin-top: 32px;
    overflow: hidden;
    white-space: nowrap;
  }

  @include e(dot) {
    display: inline-block;
    vertical-align: top;
    background-color: #3e3e3e;
    border-radius: 50%;

    @include m(max) {
      width: 10px;
      height: 10px;
    }

    @include m(small) {
      width: 4px;
      height: 4px;
      margin-top: 3px;
    }

    & + & {
      margin-left: 7px;
    }
  }

  @include e(list) {
    margin-top: 40px;
  }

  @include e(item) {
    display: inline-block;
    width: 392px;
    vertical-align: top;

    & + & {
      margin-left: ($--content-width - 392 * 3) / 2;
    }
  }

  @include e(header) {
    height: 38px;
    font-size: 16px;
    line-height: 38px;
    color: $--color-primary;
  }

  @include e(icon) {
    display: inline-block;
    width: 38px;
    height: 38px;
    margin-right: 14px;
    vertical-align: top;
    background-position: 0 -245px;

    @include background-image(url(~@/assets/img/common/common_icons.png));
  }

  @include e(header-title) {
    display: inline-block;
    height: 26px;
    line-height: 20px;
    vertical-align: middle;
    border-bottom: 1px dotted $--color-primary;
  }

  @include e(content) {
    margin-top: 16px;
  }

  @include e(img-wrapper) {
    position: relative;
    height: 230px;
    overflow: hidden;
    text-align: center;
    background-position: center 48px;

    &:hover {
      @include e(img) {
        transform: scale(1.05);
      }
    }
  }

  @include e(img) {
    width: auto;
    height: 230px;
    vertical-align: top;
    transition: transform 0.5s ease-in;
  }

  @include e(img-mask) {
    position: absolute;
    bottom: 0;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
    padding: 18px 0;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.8);
  }

  @include e(mask-line) {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 1px;
    height: 60px;
    margin-top: -30px;
    border-left: 1px solid #31353d;
  }

  @include e(mask-item) {
    display: inline-block;
    width: 50%;
    text-align: center;
    vertical-align: top;
  }

  @include e(timer) {
    margin-top: 5px;
  }

  @include e(timer-item) {
    display: inline-block;
    width: 26px;
    font-family: Helvetica Neue, Microsoft YaHei, sans-serif;
    font-size: 20px;
    line-height: 24px;
    text-align: center;
    vertical-align: top;

    &.separator {
      width: 10px;
    }

    &:first-child {
      margin-right: 10px;
    }
  }

  @include e(timer-unit) {
    font-size: 12px;
    line-height: 16px;
    color: #999;
  }

  @include e(send-amount) {
    margin-top: 5px;
    font-size: 20px;
    line-height: 26px;
  }

  @include e(table-wrapper) {
    height: 180px;
    padding: 12px 14px 0;
    background-color: #141414;
  }

  @include e(table) {
    font-size: 12px;

    tr {
      height: 24px;
      line-height: 24px;
    }

    td:first-child {
      width: 24px;
      text-align: center;
    }

    thead {
      color: #999;
    }

    tbody {
      tr {
        font-size: 13px;
        border-top: 1px solid #232529;

        & + & {
          border-top: 1px solid #232529;
        }

        &:hover {
          background-color: #282828;
        }
      }
    }

    @include m(single) {
      width: 172px;

      td:last-child {
        text-align: right;
      }
    }

    @include m(multiple) {
      width: 100%;
      text-align: center;
    }
  }

  @include e(tips-wrapper) {
    position: relative;
    height: 180px;
    background-color: #141414;

    @include e(img-mask) {
      position: relative;
    }
  }

  @include e(tips) {
    padding: 15px 20px 0;
    font-size: 13px;
    line-height: 22px;
    color: #999;
  }
}
